
#main_logo{
  background-color: rgb(255, 255, 255);
  border-radius: 65px 5px;
}
#main_logo_a_Tag{
  padding: 0px;
}
*{
  margin: 0;
  padding: 0;
  
}
#swiper-main-height{
  height: 370px;
}
a{
  text-decoration: none;
}

.box{
  width: 250px;
  background: rgb(194, 90, 194);
  box-shadow: 2px 2px 30px rgba(0,0,0,0.2);
  border-radius: 10px;
  overflow: hidden;
  /* position: absolute;
  left: 50%;
  right: 50%;
  transform: (translate(-50%,-50%)); */
}
.slide-img{
  height:250px ;
  position: relative;
}

.slide-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  box-sizing: border-box;
  box-shadow: 2px 2px 30px rgb(0 0 0 / 20%);
}
.detail-box{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.type{
  display: flex;
  flex-direction: column;
}
.type a{
  color: #222222;
  margin: 5px 0px;
  letter-spacing: 0.5px;
  padding-right: 8px;
  font-weight: 700;
}
.type span{
  color: rgba(26,26,26,0.5);
}
.price{
  color: #333333;
  font-weight: 600;
  font-size: 1.1rem;
  letter-spacing: 0.5px;
}
.overlay{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(92,95,236,0.6)
   
} 
.buy-btn{
  display: flex;
  width: 125px;
  height: 39px;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 20px;
  position: absolute;
  top: 40%;
  left: 25%;
  box-shadow: 2px 2px 30px #fd1b84;
}
.buy-btn:hover{
  color: #FFFFFF;
  background-color: #fd1b84;
  transition: all ease-out 0.2s;
}
.overlay{
  visibility: hidden;
}
.slide-img:hover .overlay{
  visibility: visible;
  animation: fade 0.5s;
}
@keyframes fade{
  0%{
      opacity: 0;
  }
  100%{
      opacity: 1;
  }
}



